}

.section--footer {
  color: #eee;
  background-color: #222;
  font-size: 12px;
  line-height: 2;
}

.section--footer.ct-section > .ct-section-inner-wrap {
  gap: 3em;
  padding: 90px 0;
}

.title-box {
  justify-content: normal;
  align-items: normal;
  gap: initial;
  background: #000 center / cover no-repeat;
  flex-flow: column;
  margin: 0 auto;
  padding: 160px 0;
  display: flex;
}

@media screen and (width <= 782px) {
  .title-box {
    padding: 80px 0;
  }
}

.title-box--sema {
  justify-content: flex-end;
  padding: 80px min(8vw, 160px);
}

.title-box--sema .ct-headline {
  margin-top: 3em;
}

.title-box--package {
  text-align: center;
  color: #fff;
  background: #000 bottom / contain no-repeat;
  gap: 2em;
  padding-bottom: min(500px, 34vw);
}

.title-box .ct-headline {
  font-size: min(7vw, 4rem);
  font-weight: 500;
  line-height: 1.3;
}

.title-box .ct-headline--monument {
  font-family: PPMonumentExtended, Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

.title-box .ct-headline b {
  font-weight: 700;
}
